* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


/* Base responsive typography and smooth scrolling */

html {
    font-size: 16px;
    scroll-behavior: smooth;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1.6;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    min-height: 100vh;
    position: relative;
    overflow-x: hidden;
}


/* Animated background particles */

body::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23ffffff' fill-opacity='0.05'%3E%3Ccircle cx='30' cy='30' r='2'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E") repeat;
    animation: float 20s ease-in-out infinite;
    pointer-events: none;
    z-index: -1;
}

@keyframes float {
    0%,
    100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-20px);
    }
}


/* Enhanced input styling with focus animations */

input:not([type=radio]) {
    width: 100%;
    height: 45px;
    padding: 0 15px;
    border: 2px solid #e1e5e9;
    border-radius: 8px;
    font-size: 16px;
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(10px);
    transition: all 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
    position: relative;
}

input:not([type=radio]):focus {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1), 0 4px 12px rgba(102, 126, 234, 0.2);
    transform: translateY(-2px);
    background: rgba(255, 255, 255, 0.95);
}

input:not([type=radio]):hover:not(:focus) {
    border-color: #a0aec0;
    transform: translateY(-1px);
}


/* Radio button enhancements */

.radio label,
input[type=radio] {
    cursor: pointer;
    transition: all 0.2s ease;
}

input[type=radio] {
    transform: scale(1.2);
    margin-right: 8px;
}

input[type=radio]:checked {
    accent-color: #667eea;
}


/* Label styling */

label {
    display: block;
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 14px;
    color: #2d3748;
    transition: color 0.2s ease;
}

label:hover {
    color: #4a5568;
}

label .mandatory {
    color: #e53e3e;
    font-size: unset;
    animation: pulse 2s infinite;
}

@keyframes pulse {
    0%,
    100% {
        opacity: 1;
    }
    50% {
        opacity: 0.7;
    }
}

.mandatory {
    color: #e53e3e;
    font-size: 12px;
    font-weight: 500;
    margin-top: 4px;
    animation: slideInFromLeft 0.3s ease;
    display: block;
}

@keyframes slideInFromLeft {
    0% {
        opacity: 0;
        transform: translateX(-10px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}


/* Enhanced textarea */

textarea {
    width: 100%;
    padding: 12px 15px;
    border: 2px solid #e1e5e9;
    border-radius: 8px;
    resize: vertical;
    font-family: inherit;
    font-size: 16px;
    line-height: 1.5;
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(10px);
    transition: all 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
    min-height: 100px;
}

textarea:focus {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1), 0 4px 12px rgba(102, 126, 234, 0.2);
    transform: translateY(-2px);
    background: rgba(255, 255, 255, 0.95);
}

textarea:hover:not(:focus) {
    border-color: #a0aec0;
    transform: translateY(-1px);
}


/* Interactive button styling */

button {
    min-width: 100px;
    height: 45px;
    padding: 0 20px;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
    position: relative;
    overflow: hidden;
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s;
}

button:hover {
    transform: translateY(-3px);
    box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

button:hover::before {
    left: 100%;
}

button:active {
    transform: translateY(-1px);
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none;
    box-shadow: 0 2px 8px rgba(102, 126, 234, 0.2);
}


/* Enhanced card styling */

.card {
    margin: 20px auto;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1), 0 8px 32px rgba(0, 0, 0, 0.08);
    border-radius: 16px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    width: 100%;
    max-width: 420px;
    position: relative;
    overflow: hidden;
    animation: slideInUp 0.6s cubic-bezier(0.4, 0.0, 0.2, 1);
}

@keyframes slideInUp {
    0% {
        opacity: 0;
        transform: translateY(60px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, #667eea, #764ba2, #f093fb);
    animation: shimmer 3s ease-in-out infinite;
}

@keyframes shimmer {
    0%,
    100% {
        transform: translateX(-100%);
    }
    50% {
        transform: translateX(100%);
    }
}

.card:hover {
    transform: translateY(-5px);
    box-shadow: 0 30px 60px rgba(0, 0, 0, 0.15), 0 12px 40px rgba(0, 0, 0, 0.1);
}

.card .form {
    padding: 40px 30px;
    position: relative;
}

.card .form {
    padding: 40px 30px;
    position: relative;
}

.card .space {
    margin: 25px 0;
}

.card .title p {
    font-weight: 700;
    font-size: 32px;
    text-align: center;
    margin-bottom: 10px;
    color: #2d3748;
    background: linear-gradient(135deg, #667eea, #764ba2);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    animation: fadeInDown 0.8s ease;
}

@keyframes fadeInDown {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.card .account {
    color: #667eea;
    cursor: pointer;
    text-decoration: none;
    font-weight: 600;
    padding: 2px 0;
    border-bottom: 2px solid transparent;
    transition: all 0.3s ease;
}

.card .account:hover {
    color: #764ba2;
    border-bottom-color: #764ba2;
    transform: translateY(-1px);
}


/* Event page responsive layout */

.eventPage {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 30px;
    min-height: 100vh;
}


/* Loading spinner */

.loading-spinner {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    border-top-color: white;
    animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}


/* Enhanced table styling */

.table-container {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    border-radius: 16px;
    padding: 20px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1), 0 8px 32px rgba(0, 0, 0, 0.08);
    overflow: hidden;
    margin: 20px 0;
    animation: slideInUp 0.6s ease;
}

.table {
    width: 100%;
    overflow-x: auto;
}

.table table {
    width: 100%;
    border-collapse: collapse;
    background: transparent;
}

.table th,
.table td {
    padding: 15px 12px;
    text-align: left;
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    transition: all 0.2s ease;
}

.table th {
    background: linear-gradient(135deg, #667eea, #764ba2);
    color: white;
    font-weight: 600;
    position: sticky;
    top: 0;
    z-index: 10;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.table tbody tr {
    transition: all 0.3s ease;
}

.table tbody tr:hover {
    background: rgba(102, 126, 234, 0.05);
    transform: scale(1.01);
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.1);
}

.table button {
    min-width: 80px;
    height: 35px;
    font-size: 12px;
    margin: 2px 4px;
    padding: 6px 12px;
    border-radius: 6px;
}


/* Success and error states */

.success-message {
    background: linear-gradient(135deg, #48bb78, #38a169);
    color: white;
    padding: 12px 16px;
    border-radius: 8px;
    margin: 10px 0;
    animation: slideInFromRight 0.5s ease;
}

.error-message {
    background: linear-gradient(135deg, #e53e3e, #c53030);
    color: white;
    padding: 12px 16px;
    border-radius: 8px;
    margin: 10px 0;
    animation: slideInFromRight 0.5s ease;
}

@keyframes slideInFromRight {
    0% {
        opacity: 0;
        transform: translateX(30px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}


/* Form groups for better spacing */

.form-group {
    margin-bottom: 20px;
    animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


/* Interactive form rows */

.form-row {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    align-items: flex-start;
}

.form-row>div {
    flex: 1;
    min-width: 200px;
}


/* Mobile responsive adjustments */

@media (max-width: 767px) {
    body::before {
        animation: float 10s ease-in-out infinite;
    }
    .card {
        margin: 10px;
        border-radius: 12px;
        max-width: calc(100% - 20px);
    }
    .card .form {
        padding: 25px 20px;
    }
    .card .title p {
        font-size: 28px;
    }
    .eventPage {
        padding: 15px;
    }
    .form-row {
        flex-direction: column;
        gap: 10px;
    }
    .form-row>div {
        width: 100% !important;
        min-width: unset;
    }
    .table-container {
        padding: 15px;
        border-radius: 12px;
    }
    /* Mobile card-style table */
    .table-responsive table,
    .table-responsive thead,
    .table-responsive tbody,
    .table-responsive th,
    .table-responsive td,
    .table-responsive tr {
        display: block;
    }
    .table-responsive thead tr {
        position: absolute;
        top: -9999px;
        left: -9999px;
    }
    .table-responsive tr {
        border: 1px solid rgba(102, 126, 234, 0.1);
        margin-bottom: 15px;
        padding: 15px;
        border-radius: 12px;
        background: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(10px);
        box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
        transition: all 0.3s ease;
    }
    .table-responsive tr:hover {
        transform: translateY(-2px);
        box-shadow: 0 8px 25px rgba(102, 126, 234, 0.15);
    }
    .table-responsive td {
        border: none;
        position: relative;
        padding-left: 40% !important;
        padding-top: 12px;
        padding-bottom: 12px;
        word-wrap: break-word;
    }
    .table-responsive td:before {
        content: attr(data-label) ": ";
        position: absolute;
        left: 6px;
        width: 35%;
        padding-right: 10px;
        white-space: nowrap;
        font-weight: 600;
        color: #4a5568;
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .card {
        max-width: 500px;
    }
    .card .form {
        padding: 35px 25px;
    }
    .eventPage {
        width: 95%;
        padding: 25px;
    }
}

@media (min-width: 1024px) {
    .card {
        max-width: 450px;
    }
    .card .form {
        padding: 40px;
    }
    .eventPage {
        width: 90%;
        max-width: 1200px;
    }
    .table-container:hover {
        box-shadow: 0 30px 60px rgba(0, 0, 0, 0.15), 0 12px 40px rgba(0, 0, 0, 0.1);
    }
}

.card .space {
    margin: 15px 0;
}

.card .title p {
    font-weight: bold;
    font-size: 24px;
}

.card .account {
    color: blue;
    cursor: pointer;
}

.eventPage {
    width: 70%;
    margin: auto;
}

.table {
    width: 100%;
}

.table button {
    padding: 2px;
    width: 50px;
    height: unset;
}

table,
th,
td {
    border: 1px solid black;
    border-collapse: collapse;
}

th,
td {
    padding: 3px 5px;
}

th {
    text-align: left;
}


/* Password Input Wrapper */

.password-input-wrapper {
    position: relative;
    display: flex;
    align-items: center;
}

.password-input-wrapper input {
    padding-right: 45px !important;
    flex: 1;
}

.password-toggle {
    position: absolute;
    right: 10px;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 16px;
    padding: 5px;
    border-radius: 4px;
    transition: background-color 0.3s ease;
    z-index: 2;
}

.password-toggle:hover {
    background-color: rgba(255, 255, 255, 0.1);
}

.password-toggle:disabled {
    cursor: not-allowed;
    opacity: 0.5;
}


/* Password Strength Indicator */

.password-strength {
    margin-top: 8px;
    padding: 8px;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(10px);
}

.strength-bar {
    width: 100%;
    height: 4px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 2px;
    overflow: hidden;
    margin-bottom: 4px;
}

.strength-fill {
    height: 100%;
    border-radius: 2px;
    transition: width 0.3s ease, background-color 0.3s ease;
}

.strength-fill.weak {
    width: 33%;
    background: linear-gradient(90deg, #ff4757, #ff6b7a);
}

.strength-fill.medium {
    width: 66%;
    background: linear-gradient(90deg, #ffa502, #ffb84d);
}

.strength-fill.strong {
    width: 100%;
    background: linear-gradient(90deg, #2ed573, #7bed9f);
}

.strength-text {
    font-size: 12px;
    text-transform: capitalize;
    font-weight: 500;
}

.password-strength.weak .strength-text {
    color: #ff6b7a;
}

.password-strength.medium .strength-text {
    color: #ffb84d;
}

.password-strength.strong .strength-text {
    color: #7bed9f;
}


/* Input Groups */

.input-group {
    margin-bottom: 1rem;
}

.input-group label {
    display: block;
    margin-bottom: 0.5rem;
}


/* Label Container for Right-side Mandatory Asterisk */

.label-container {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
}

.label-container label {
    margin-bottom: 0;
}

.label-container .mandatory {
    color: #e53e3e;
    font-size: 14px;
    font-weight: 600;
    animation: pulse 2s infinite;
    margin-left: 4px;
}


/* Error Message Container */

.error-message {
    padding: 12px;
    margin-bottom: 16px;
    border-radius: 8px;
    background: rgba(255, 71, 87, 0.1);
    border-left: 4px solid #ff4757;
    backdrop-filter: blur(10px);
}


/* Input Error State */

input.error {
    border-color: #ff4757 !important;
    box-shadow: 0 0 0 2px rgba(255, 71, 87, 0.2) !important;
}

input.error:focus {
    box-shadow: 0 0 0 3px rgba(255, 71, 87, 0.3) !important;
}


/* Slide-in Animation */

@keyframes slideIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.animate-slide-in {
    animation: slideIn 0.3s ease-out;
}


/* Loading Button States */

button.loading {
    position: relative;
    color: transparent !important;
}

button .spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 16px;
    height: 16px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-top: 2px solid #ffffff;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}


/* EventPage Controls */

.controls-section {
    margin-bottom: 25px;
    padding: 20px;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    border-radius: 15px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
}

.search-controls {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-bottom: 15px;
}

.search-input-wrapper {
    position: relative;
    flex: 1;
}

.search-input {
    width: 100%;
    padding: 12px 20px;
    border: 2px solid rgba(102, 126, 234, 0.2);
    border-radius: 25px;
    background: rgba(255, 255, 255, 0.9);
    font-size: 16px;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.search-input:focus {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
    transform: translateY(-1px);
}

.filter-controls {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
}

.filter-select,
.sort-select {
    padding: 10px 15px;
    border: 2px solid rgba(102, 126, 234, 0.2);
    border-radius: 20px;
    background: rgba(255, 255, 255, 0.9);
    font-size: 14px;
    cursor: pointer;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.filter-select:focus,
.sort-select:focus {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.clear-filters-btn {
    padding: 10px 20px;
    background: linear-gradient(135deg, #ff6b7a, #ff4757);
    color: white;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-size: 14px;
    transition: all 0.3s ease;
    white-space: nowrap;
}

.clear-filters-btn:hover {
    transform: translateY(-1px);
    box-shadow: 0 5px 15px rgba(255, 71, 87, 0.4);
}

.results-info {
    text-align: center;
    color: #666;
    font-size: 14px;
    font-weight: 500;
    padding: 10px;
    background: rgba(102, 126, 234, 0.1);
    border-radius: 20px;
    backdrop-filter: blur(10px);
}


/* Loading States */

.loading-container {
    text-align: center;
    padding: 60px 20px;
    color: #667eea;
}

.spinner-large {
    width: 40px;
    height: 40px;
    border: 4px solid rgba(102, 126, 234, 0.2);
    border-top: 4px solid #667eea;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin: 0 auto 20px;
}


/* Table Enhancements */

.price-tag {
    background: linear-gradient(135deg, #2ed573, #7bed9f);
    color: white;
    padding: 4px 12px;
    border-radius: 15px;
    font-weight: 600;
    font-size: 14px;
}

.type-badge {
    background: linear-gradient(135deg, #667eea, #764ba2);
    color: white;
    padding: 4px 12px;
    border-radius: 15px;
    font-weight: 500;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.action-buttons {
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
}

.edit-btn {
    background: linear-gradient(135deg, #ffa502, #ff6348);
    color: white;
    border: none;
    padding: 6px 12px;
    border-radius: 15px;
    cursor: pointer;
    font-size: 12px;
    transition: all 0.3s ease;
    white-space: nowrap;
}

.edit-btn:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(255, 165, 2, 0.4);
}

.delete-btn {
    background: linear-gradient(135deg, #ff4757, #c44569);
    color: white;
    border: none;
    padding: 6px 12px;
    border-radius: 15px;
    cursor: pointer;
    font-size: 12px;
    transition: all 0.3s ease;
    white-space: nowrap;
}

.delete-btn:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(255, 71, 87, 0.4);
}


/* Empty States */

.empty-state {
    text-align: center;
    padding: 60px 20px;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    border-radius: 20px;
    margin: 20px 0;
}

.empty-icon {
    font-size: 4rem;
    margin-bottom: 20px;
    opacity: 0.7;
}

.empty-state h3 {
    color: #4a5568;
    margin-bottom: 10px;
    font-size: 1.5rem;
}

.empty-state p {
    color: #718096;
    margin-bottom: 25px;
    font-size: 1.1rem;
}

.create-first-event-btn {
    background: linear-gradient(135deg, #667eea, #764ba2);
    color: white;
    border: none;
    padding: 15px 30px;
    border-radius: 25px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    transition: all 0.3s ease;
}

.create-first-event-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(102, 126, 234, 0.3);
}


/* Fade-in Animation for Table Rows */

.animate-fade-in {
    animation: fadeIn 0.5s ease-in-out;
}


/* Responsive Controls */

@media (min-width: 768px) {
    .search-controls {
        flex-direction: row;
        align-items: flex-start;
    }
    .filter-controls {
        flex-wrap: nowrap;
    }
    .action-buttons {
        flex-direction: row;
    }
}


/* EventForm Enhancements */

.success-message {
    padding: 12px;
    margin-bottom: 16px;
    border-radius: 8px;
    background: rgba(46, 213, 115, 0.1);
    border-left: 4px solid #2ed573;
    backdrop-filter: blur(10px);
    color: #27ae60;
    font-weight: 600;
}

.success-text {
    color: #27ae60;
    font-size: 12px;
    font-weight: 500;
    margin-top: 5px;
    display: block;
}

.character-count {
    font-size: 12px;
    color: #718096;
    text-align: right;
    margin-top: 5px;
}

.price-input-wrapper {
    position: relative;
    display: flex;
    align-items: center;
}

.currency-symbol {
    position: absolute;
    left: 8px;
    color: #4a5568;
    font-weight: 600;
    z-index: 2;
}


/* Enhanced Radio Buttons */

.radio-group {
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 10px 0;
}

.radio-option {
    position: relative;
}

.radio-label {
    display: flex;
    align-items: flex-start;
    cursor: pointer;
    padding: 15px;
    border: 2px solid rgba(102, 126, 234, 0.2);
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    transition: all 0.3s ease;
    position: relative;
    min-height: 60px;
}

.radio-label:hover {
    border-color: #667eea;
    transform: translateY(-1px);
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.1);
}

.radio-label input[type="radio"] {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

.radio-custom {
    width: 20px;
    height: 20px;
    border: 2px solid #667eea;
    border-radius: 50%;
    margin-right: 15px;
    position: relative;
    transition: all 0.3s ease;
    flex-shrink: 0;
    margin-top: 2px;
}

.radio-custom::after {
    content: '';
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #667eea;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: transform 0.3s ease;
}

.radio-label input[type="radio"]:checked~.radio-custom::after {
    transform: translate(-50%, -50%) scale(1);
}

.radio-label input[type="radio"]:checked~.radio-custom {
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.radio-text {
    font-weight: 600;
    color: #2d3748;
    margin-bottom: 4px;
    display: block;
}

.radio-description {
    font-size: 14px;
    color: #718096;
    line-height: 1.4;
}

.radio-label input[type="radio"]:checked {
    background: linear-gradient(135deg, #667eea, #764ba2);
}

.radio-label input[type="radio"]:checked~.radio-text {
    color: #667eea;
}


/* Input States */

input.success,
textarea.success {
    border-color: #2ed573 !important;
    box-shadow: 0 0 0 2px rgba(46, 213, 115, 0.2) !important;
}

input.success:focus,
textarea.success:focus {
    box-shadow: 0 0 0 3px rgba(46, 213, 115, 0.3) !important;
}


/* Secondary Button */

.secondary-btn {
    background: rgba(255, 255, 255, 0.9) !important;
    color: #4a5568 !important;
    border: 2px solid rgba(102, 126, 234, 0.3) !important;
    backdrop-filter: blur(10px);
}

.secondary-btn:hover {
    background: rgba(102, 126, 234, 0.1) !important;
    border-color: #667eea !important;
    transform: translateY(-1px);
}

.secondary-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}


/* Responsive Radio Buttons */

@media (min-width: 768px) {
    .radio-group {
        flex-direction: row;
    }
    .radio-option {
        flex: 1;
    }
}


/* Profile Header Enhancements */

.profile-header {
    background: linear-gradient(135deg, #2c3e50, #34495e);
    color: white;
    padding: 15px 30px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
    margin-bottom: 20px;
    position: relative;
    backdrop-filter: blur(10px);
}

.profile-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    gap: 20px;
}

.app-title {
    font-size: 24px;
    font-weight: bold;
    color: #ecf0f1;
    display: flex;
    align-items: center;
    gap: 10px;
}

.app-icon {
    font-size: 28px;
    animation: float 3s ease-in-out infinite;
}

.nav-section {
    display: flex;
    align-items: center;
    gap: 20px;
}

.profile-section {
    display: flex;
    align-items: center;
    gap: 15px;
}


/* Profile Display Mode */

.profile-display-mode {
    display: flex;
    align-items: center;
    gap: 12px;
    background: rgba(52, 73, 94, 0.8);
    padding: 10px 18px;
    border-radius: 30px;
    transition: all 0.3s ease;
    cursor: pointer;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.profile-display-mode:hover {
    background: rgba(74, 95, 122, 0.9);
    transform: translateY(-1px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.profile-avatar {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: linear-gradient(135deg, #3498db, #2980b9);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 16px;
    box-shadow: 0 2px 8px rgba(52, 152, 219, 0.3);
}

.profile-name {
    font-size: 16px;
    font-weight: 500;
    color: #ecf0f1;
    min-width: 60px;
}

.edit-btn {
    background: transparent;
    color: #bdc3c7;
    border: none;
    padding: 6px;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s ease;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.edit-btn:hover {
    color: #ecf0f1;
    background: rgba(255, 255, 255, 0.1);
    transform: rotate(15deg);
}

.edit-icon {
    font-size: 14px;
}


/* Profile Edit Mode */

.profile-edit-mode {
    display: flex;
    align-items: flex-start;
    gap: 12px;
    background: rgba(44, 62, 80, 0.95);
    padding: 15px;
    border-radius: 15px;
    backdrop-filter: blur(15px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    min-width: 300px;
}

.edit-input-wrapper {
    flex: 1;
}

.profile-name-input {
    font-size: 16px;
    padding: 10px 15px;
    border: 2px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.9);
    color: #2c3e50;
    outline: none;
    width: 100%;
    transition: all 0.3s ease;
}

.profile-name-input:focus {
    border-color: #3498db;
    box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
    transform: translateY(-1px);
}

.profile-name-input.error {
    border-color: #e74c3c;
    box-shadow: 0 0 0 3px rgba(231, 76, 60, 0.2);
}

.profile-error {
    color: #e74c3c;
    font-size: 12px;
    margin-top: 5px;
    display: block;
}

.edit-actions {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.save-btn {
    background: linear-gradient(135deg, #27ae60, #2ecc71);
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 500;
    transition: all 0.3s ease;
    white-space: nowrap;
    display: flex;
    align-items: center;
    gap: 6px;
    justify-content: center;
    min-width: 80px;
    min-height: 36px;
}

.save-btn:hover:not(:disabled) {
    background: linear-gradient(135deg, #2ecc71, #27ae60);
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(39, 174, 96, 0.3);
}

.save-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}

.cancel-btn {
    background: linear-gradient(135deg, #e74c3c, #c0392b);
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 500;
    transition: all 0.3s ease;
    white-space: nowrap;
    display: flex;
    align-items: center;
    gap: 6px;
    justify-content: center;
    min-width: 80px;
    min-height: 36px;
}

.cancel-btn:hover:not(:disabled) {
    background: linear-gradient(135deg, #c0392b, #e74c3c);
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(231, 76, 60, 0.3);
}

.cancel-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}


/* Logout Button */

.logout-btn {
    background: linear-gradient(135deg, #e74c3c, #c0392b);
    color: white;
    border: none;
    padding: 12px 24px;
    border-radius: 25px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 500;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 10px;
    min-width: 120px;
    justify-content: center;
    white-space: nowrap;
    min-height: 48px;
}

.logout-btn:hover:not(:disabled) {
    background: linear-gradient(135deg, #c0392b, #e74c3c);
    transform: translateY(-1px);
    box-shadow: 0 6px 20px rgba(231, 76, 60, 0.3);
}

.logout-btn:disabled {
    opacity: 0.7;
    cursor: not-allowed;
}

.logout-icon {
    font-size: 18px;
}


/* Small Spinner */

.spinner-small {
    width: 14px;
    height: 14px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-top: 2px solid #ffffff;
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
}


/* Notifications */

.notification {
    position: absolute;
    top: -50px;
    left: 50%;
    transform: translateX(-50%);
    padding: 12px 24px;
    border-radius: 25px;
    font-size: 14px;
    font-weight: 500;
    z-index: 1000;
    backdrop-filter: blur(15px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.notification.success {
    background: rgba(39, 174, 96, 0.9);
    color: white;
}

.notification.info {
    background: rgba(52, 152, 219, 0.9);
    color: white;
}

.notification.error {
    background: rgba(231, 76, 60, 0.9);
    color: white;
}


/* Responsive Profile */

@media (max-width: 768px) {
    .profile-header {
        padding: 12px 15px;
    }
    .profile-container {
        flex-direction: column;
        gap: 15px;
    }
    .app-title {
        font-size: 20px;
        text-align: center;
    }
    .nav-section {
        justify-content: center;
    }
    .profile-section {
        flex-direction: column;
        gap: 12px;
        width: 100%;
    }
    .profile-edit-mode {
        min-width: unset;
        width: 100%;
        max-width: 300px;
    }
    .edit-actions {
        flex-direction: row;
    }
}

@media (max-width: 480px) {
    .profile-display-mode {
        padding: 8px 12px;
        gap: 8px;
    }
    .logout-btn {
        padding: 10px 20px;
        font-size: 14px;
        min-width: 100px;
    }
    .app-title {
        font-size: 18px;
    }
}